.images-container {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    height: 500px;
    max-height: 30vh;
    padding: 16px 20px;
    overflow: auto;
    background: var(--clr-fg);
    border-radius: 6px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.images-container:after {
    content: '';
    padding-right: 20px;
}

.uploaded-image {
    height: 100%;
    width: auto;
    box-shadow: 0 3px 6px rgba(0,0,0,0.26), 0 3px 6px rgba(0,0,0,0.3);
}

.uploaded-image:not(:first-child) {
    margin-left: 20px;
}

.buttons-container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 20px;
}

.button {
    display: inline-block;
    background-color: #4CAF50;
    color: white;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
    padding: 10px 20px;
    border: none;
    border-radius: 3px;
    box-sizing: border-box;
    box-shadow: 0 3px 6px rgba(0,0,0,0.3)
}

.button:focus {
    outline: none;
}

.button:active {
    filter: brightness(0.9);
}

.button:disabled {
    background-color: #ababab;
}

.button:not(:disabled) {
    cursor: pointer;
}

.button:not(:first-child) {
    margin-left: 8px;
}

.button:first-child {
    margin-right: 16px;
}

.h1main {
    font-size: 20px;
}